<template>
  <div class="wrapper">
    <div class="container">
      <div class="content-title">
        <div class="title">钱包详情</div>
        <div class="tx">
          地址:
          <span>{{copyUrl}}</span>
          <span
            v-clipboard:copy="copyUrl"
            v-clipboard:success="onCopy"
            v-clipboard:error="onError"
            class="copy-wrapper"
          >
            <img src="../../assets/index_img/copy.svg" alt />
          </span>
        </div>
      </div>
      <div class="detail-content">
        <div class="item-content">
          <div class="m-item">
            <div class="detail-item">
              <div class="el-tooltip">余额</div>
              <div class="s-item-content-text">{{(balance/100000000).toFixed(2)}}{{unit}}</div>
            </div>
            <div class="detail-item">
              <div class="el-tooltip">交易数</div>
              <div class="s-item-content-text">{{allNum}}</div>
            </div>
            <div v-show="hidekey" class="detail-item">
              <div class="el-tooltip">公钥</div>
              <div class="s-item-content-text">{{copyUrlkey}}</div>
            </div>
            <!-- <div class="detail-item">
              <div class="el-tooltip">可提取的 ONG</div>
              <div class="s-item-content-text">0</div>
            </div>
            <div class="detail-item">
              <div class="el-tooltip">未解绑的 ONG</div>
              <div class="s-item-content-text">0</div>
            </div>-->
          </div>
        </div>
      </div>
      <div class="tradeTable">
        <div class="tradeTitle">交易列表</div>
        <div class="tableWrapper">
          <div class="tableminwrapper">
            <el-table :data="dealList" style="width: 100%">
              <el-table-column label="ID" width="180">
                <template scope="scoped">
                  <span @click="todetails(scoped.row)" style="cursor:pointer">
                    {{`${scoped.row.id.substring(0, 6)}...${scoped.row.id.substring(
                    scoped.row.id.length - 6
                    )}`}}
                  </span>
                </template>
              </el-table-column>
              <el-table-column prop="timestamp.human" label="时间" width="180"></el-table-column>
              <el-table-column label="付款地址">
                <template scope="scoped">
                  <span @click="towalletdetails(scoped.row.sender)" style="cursor:pointer">
                    {{`${scoped.row.sender.substring(
                    0,
                    6
                    )}...${scoped.row.sender.substring(scoped.row.sender.length - 6)}`}}
                  </span>
                </template>
              </el-table-column>
              <el-table-column label="收款地址">
                <template scope="scoped">
                  <span
                    style="cursor:pointer"
                    v-show="scoped.row.type!==6"
                    @click="towalletdetails(scoped.row.recipient)"
                  >
                    {{`${scoped.row.recipient.substring(
                    0,
                    6
                    )}...${scoped.row.recipient.substring(scoped.row.recipient.length - 6)}`}}
                  </span>
                  <span
                    style="cursor:pointer"
                    v-show="scoped.row.type==6&&copyUrl==scoped.row.sender"
                    @click="openmintable(scoped.row)"
                  >Multipayment</span>
                  <span
                    style="cursor:pointer"
                    v-show="scoped.row.type==6&&copyUrl!==scoped.row.sender"
                    @click="towalletdetails(scoped.row.recipient)"
                  >
                    {{`${copyUrl.substring(
                    0,
                    6
                    )}...${copyUrl.substring(copyUrl.length - 6)}`}}
                  </span>
                </template>
              </el-table-column>
              <el-table-column label="交易额">
                <template scope="scoped">{{ (scoped.row.amount/100000000).toFixed(2)}}{{unit}}</template>
              </el-table-column>
              <el-table-column label="手续费">
                <template scope="scoped">{{(scoped.row.fee/100000000).toFixed(2)}}{{unit}}</template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </div>
      <div class="changepage-wrapper">
        <el-pagination
          @current-change="pagechange"
          :pager-count="5"
          :total="allNum"
          :page-size="100"
          layout="prev, pager, next"
        ></el-pagination>
      </div>
    </div>
    <el-dialog width="80%" title="Multipayment" :visible.sync="dialogTableVisible">
      <el-table :data="gridData">
        <el-table-column property="recipientId" label="接收地址" width="350">
          <template scope="scoped">
            <span
              style="cursor:pointer"
              @click="towalletdetails(scoped.row.recipientId)"
            >{{scoped.row.recipientId}}</span>
          </template>
        </el-table-column>
        <el-table-column label="交易额" width="200">
          <template scope="scoped">{{(scoped.row.amount/100000000).toFixed(2)}}{{unit}}</template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import {
  getwallet,
  getdealdetail,
  getdealdetailnn,
  // getblockdetailheight
} from "../../../api";
import moment from "moment/moment";
export default {
  mounted() {
    this.unit = sessionStorage.getItem("unit")
      ? sessionStorage.getItem("unit")
      : "MCN";
    var id = this.$route.query;
    getwallet(id.id)
      .then((res) => {
        this.balance = res.data.data.balance;
        this.copyUrl = res.data.data.address;
        if (res.data.data.publicKey == undefined) {
          this.publicKey = "0000000000000000000";
          this.hidekey = false;
        } else {
          this.publicKey = res.data.data.publicKey;
          this.hidekey = true;
        }
        this.copyUrlkey = res.data.data.publicKey;
        //  console.log(this.publicKey)
      })
      .catch((err) => {
        window.console.log(err);
      });
    getdealdetail(id.id, 1, 100)
      .then((res) => {
        this.allNum = res.data.meta.totalCount;
        for (let v of res.data.data) {
          v.timestamp.human = moment(v.timestamp.human).format(
            "YYYY-MM-DD HH:mm:ss"
          );
          if (v.type == 6 && this.copyUrl == v.sender) {
            for (let r of v.asset.payments) {
              v.amount = v.amount * 1 + r.amount * 1;
            }
          }
          if (v.type == 6 && this.copyUrl !== v.sender) {
            for (let r of v.asset.payments) {
              if (this.copyUrl == r.recipientId) v.amount = r.amount;
            }
          }
        }
        console.log(res);
        this.dealList = res.data.data;
        // this.lastTime = res.data.data[0].timestamp.human;
      })

      .catch((err) => {
        window.console.log(err);
      });

    getdealdetailnn(id.id)
      .then((res) => {
        console.log(res);
        for (let v of res.data.data) {
          this.alldealAmount = this.alldealAmount + v.amount * 1;
        }
      })

      .catch((err) => {
        window.console.log(err);
      });
  },
  data() {
    return {
      dealList: [],
      copyUrl: "",
      showqrcode: false,
      balance: "",
      allNum: 0,
      copyUrlkey: "",
      alldealAmount: "",
      publicKey: "",
      hidekey: true,
      unit: "",
      allpage: 0,
      dialogTableVisible: false,
      gridData: [],
    };
  },
  methods: {
    onCopy() {
      this.$message({
        message: "复制成功",
        type: "success",
      });
    },
    onError() {
      this.$message.error("复制失败");
    },
    pagechange(e) {
      var id = this.$route.query;
      // window.console.log(e);

      getdealdetail(id.id, e, 100)
        .then((res) => {
          console.log(res);
          for (let v of res.data.data) {
            v.timestamp.human = moment(v.timestamp.human).format(
              "YYYY-MM-DD HH:mm:ss"
            );
            if (v.type == 6 && this.copyUrl == v.sender) {
              for (let r of v.asset.payments) {
                v.amount = v.amount * 1 + r.amount * 1;
              }
            }
            if (v.type == 6 && this.copyUrl !== v.sender) {
              for (let r of v.asset.payments) {
                if (this.copyUrl == r.recipientId) v.amount = r.amount;
              }
            }
          }

          this.dealList = res.data.data;
          // this.lastTime = res.data.data[0].timestamp.human;
        })

        .catch((err) => {
          window.console.log(err);
        });
    },
    towalletdetails(address) {
      this.$router.push({
        path: "/walletdetails",
        query: {
          id: address,
        },
      });
      window.location.reload();
    },
    openmintable(r) {
      this.dialogTableVisible = true;
      console.log(r.asset.payments);
      this.gridData = r.asset.payments;
    },
    todetails(id) {
      this.$router.push({
        path: "/tradedetails",
        query: {
          id: id.id,
        },
      });
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
};
</script>

